$tim: #87509c; //Tím
$ngoc: #17c2a4; // Cyan
$vang: #ffdd99; // Vang
$do: #d74680; // Đỏ
$xanh: #3c5499; // Xanh
$cam: #ebc985; // Cam

// Slider ở trang chủ
header {
    //padding: 40px 0
    background-color: $tim;
    nav {
        min-height: 120px;
        display: flex;
        align-items: center;
        text-align: center;
        li {
            display: inline-block;
            margin-top: 15px;
            text-align: center;
            border-radius: 5px;
            padding: 3px 0 6px 0;
            &:hover {
                background: #643a79;
                text-align: center; }
            a {
                color: #fff;
                text-decoration: none;
                padding: 0 10px;
                &:hover {
                    text-decoration: none;
                    color: #fff; } } } } }
.home_slider {
    background: $tim;
    min-height: 368px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h1 {
        font-size: 1.1rem;
        color: #fff; }
    .btn-more {
        color: black;
        background: $vang;
        text-transform: uppercase;
        font-size: .8rem; } }

// Các dịch vụ
.home_services {
    background: $ngoc;
    min-height: 368px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 3rem;
    color: #fff;
    p {
        color: lighten($ngoc, 20%); }
    h2 {
        position: relative;
        &:before {
            content: '';
            display: block;
            height: 2px;
            width: 60px;
            background: black;
            position: absolute;
            left: 50%;
            bottom: -6px;
            transform: translateX(-50%); } } }
.home_meet {
    background: lighten($ngoc, 53%);
    in-height: 368px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 3rem;
    color: black;
    h2 {
        position: relative;
        padding-bottom: .5rem;
        &:before {
            content: '';
            display: block;
            height: 3px;
            background: darken($ngoc, 7%);
            width: 55px;
            position: absolute;
            left: 50%;
            bottom: 0;
            transform: translateX(-50%); } }
    img {
        min-width: 150px;
        border-radius: 50%; }
    .text {
        color: $ngoc; }
    .font {
        font-size: 1.25rem;
        padding: 1rem; }
    ul {
        display: block;
        margin: 0;
        padding: 0;
        list-style: none;
        text-align: center;
        li {
            display: inline-block;
            background: darken(lighten($ngoc, 53), 55);
            border-radius: 50%;
            width: 25px;
            height: 25px;
            margin: auto 3px;
            a {
                color: #fff; } } } }
.home_skills {
    min-height: 368px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 3rem;
    color: black;
    h2 {
        position: relative;
        padding-bottom: .5rem;
        &:before {
            content: '';
            display: block;
            height: 3px;
            background: darken($ngoc, 7%);
            width: 55px;
            position: absolute;
            left: 50%;
            bottom: 0;
            transform: translateX(-50%); } }
    .progress-circle {
        position: relative;
        display: inline-block;
        margin: 1rem;
        width: 140px;
        height: 140px;
        border-radius: 50%;
        background-color: #ebebeb;
        &:after {
            content: '';
            display: inline-block;
            width: 100%;
            height: 100%;
            border-radius: 50%;
            -webkit-animation: colorload 2s;
            animation: colorload 2s; }
        span {
            font-size: 1.5rem;
            color: #8b8b8b;
            position: absolute;
            left: 50%;
            top: 50%;
            display: block;
            width: 120px;
            height: 120px;
            line-height: 120px;
            margin-left: -60px;
            margin-top: -60px;
            text-align: center;
            border-radius: 50%;
            background: #fff;
            z-index: 1; } }
    .progress-90 {
        &:after {
            background-image: linear-gradient(54deg, #30bae7 50%, transparent 50%, transparent), linear-gradient(270deg, #30bae7 50%, #ebebeb 50%, #ebebeb); } }
    .progress-75 {
        &:after {
            background-image: linear-gradient(0deg, #fc3154 50%, transparent 50%, transparent), linear-gradient(270deg, #fc3154 50%, #ebebeb 50%, #ebebeb); } }
    .progress-70 {
        &:after {
            background-image: linear-gradient(-18deg, #15c7a8 50%, transparent 50%, transparent), linear-gradient(270deg, #15c7a8 50%, #ebebeb 50%, #ebebeb); } }
    .progress-85 {
        &:after {
            background-image: linear-gradient(36deg, #eb7d4b 50%, transparent 50%, transparent), linear-gradient(270deg, #eb7d4b 50%, #ebebeb 50%, #ebebeb); } }

    @-webkit-keyframes colorload {
        0% {
            opacity: 0;
            transform: rotate(0deg); }
        100% {
            opacity: 1;
            transform: rotate(360deg); } } }
